import Link from "next/link";
import { Container, Navbar, NavItem } from "react-materialize";

const Nav = () => (
  <Navbar brand="SwapSale" right>
    <Link href="/about">
      <NavItem>About</NavItem>
    </Link>
    <Link href="/login">
      <NavItem>Log In</NavItem>
    </Link>
  </Navbar>
);

export default Nav;
